html,
body {
	height: 100%;
}

body {
	display: flex;
	flex-direction: column;
	font-size: 20px;
	line-height: 1.2;
	color: #000;

	//отключаем скролл (модалки)
	.lock & {
		overflow: hidden;
		touch-action: none;
	}
}

.page {
	font-family: var(--font-family, sans-serif);
	-webkit-text-size-adjust: 100%;
	scroll-behavior: smooth;
}

.page__body {
	margin: 0;
	min-width: 320px;
	min-height: 100%;
}

.wrapper {
	min-height: 100%;
	display: flex;
	flex-direction: column;
	overflow: hidden; // если используете на сайте position: sticky - уберите эту настройку

	//прижимаем футер
	> main {
		flex: 1 1 auto;
	}

	//фикс для swiper js
	> * {
		min-width: 0;
	}
}

.container {
	max-width: var(--container-width);
	padding: 0 var(--container-offset);
	margin: 0 auto;
}

.container--content-wide {
	max-width: var(--container-width--wide);
}

.container--content-narrow {
	max-width: var(--container-width--narrow);
}

.logo {
	&__img {
		object-fit: contain;
	}
}

.sr-only {
	position: absolute;
	width: 1px;
	height: 1px;
	margin: -1px;
	border: 0;
	padding: 0;
	white-space: nowrap;
	clip-path: inset(100%);
	clip: rect(0 0 0 0);
	overflow: hidden;
}

.checkbox-hidden,
.radio-hidden {
	width: 1px;
	height: 1px;
	position: absolute;
	overflow: hidden;
	clip: rect(0 0 0 0);
}
