:root {
  --font-family: "FuturaNew", sans-serif;
  --content-width: 1750px;
  --content-width--wide: 1640px;
  --content-width--narrow: 1350px;
  --container-offset: 15px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));
  --container-width--wide: calc(var(--content-width--wide) + (var(--container-offset) * 2));
  --container-width--narrow: calc(var(--content-width--narrow) + (var(--container-offset) * 2));
  --light-color: #fff;
  --black-color: #080606;
  --main-color: #59443f;
  --main-color: #ffc59e;
  --grey-color: #c3b9b5;
  --grey-light-color: #ede9e8;
  --bg-color: #fbf8f8;
}

/* stylelint-disable */
/* stylelint-disable */
.custom-checkbox__field:checked + .custom-checkbox__content::after {
  opacity: 1;
}

.custom-checkbox__field:focus + .custom-checkbox__content::before {
  outline: 2px solid #f00;
  outline-offset: 2px;
}

.custom-checkbox__field:disabled + .custom-checkbox__content {
  opacity: 0.4;
  pointer-events: none;
}

@font-face {
  font-family: "FuturaNew";
  src: url("../fonts/FuturaNew-Bold.woff2") format("woff2"), url("../FuturaNew-Bold.woff") format("woff");
  font-weight: 700;
  font-display: swap;
  font-style: normal;
}
@font-face {
  font-family: "SchoolBook";
  src: url("../fonts/SchoolBook-Regular.woff2") format("woff2"), url("../SchoolBook-Regular.woff") format("woff");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}
@font-face {
  font-family: "FuturaNew";
  src: url("../fonts/FuturaNew-Regular.woff2") format("woff2"), url("../FuturaNew-Regular.woff") format("woff");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}
html,
body {
  height: 100%;
}

body {
  display: flex;
  flex-direction: column;
  font-size: 20px;
  line-height: 1.2;
  color: #000;
}
.lock body {
  overflow: hidden;
  touch-action: none;
}

.page {
  font-family: var(--font-family, sans-serif);
  -webkit-text-size-adjust: 100%;
  scroll-behavior: smooth;
}

.page__body {
  margin: 0;
  min-width: 320px;
  min-height: 100%;
}

.wrapper {
  min-height: 100%;
  display: flex;
  flex-direction: column;
  overflow: hidden;
}
.wrapper > main {
  flex: 1 1 auto;
}
.wrapper > * {
  min-width: 0;
}

.container {
  max-width: var(--container-width);
  padding: 0 var(--container-offset);
  margin: 0 auto;
}

.container--content-wide {
  max-width: var(--container-width--wide);
}

.container--content-narrow {
  max-width: var(--container-width--narrow);
}

.logo__img {
  object-fit: contain;
}

.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.checkbox-hidden,
.radio-hidden {
  width: 1px;
  height: 1px;
  position: absolute;
  overflow: hidden;
  clip: rect(0 0 0 0);
}

.btn {
  border-radius: 30px;
  padding: 24px 77px;
  color: var(--light-color);
  text-transform: uppercase;
  font-weight: 700;
}
.btn--brown {
  background-color: var(--main-color);
  box-shadow: 0px 4px 20px var(--main-color);
}
.btn--grey {
  background-color: var(--grey-light-color);
  box-shadow: 0px 4px 20px var(--grey-light-color);
}
.btn--up {
  background-color: var(--light-color);
  border-radius: 50px;
  color: var(--main-color);
  font-weight: 400;
  padding: 11px 46px;
}